<template>
	<div>
		<app-header></app-header>		
		<div class="content">			
			<div class="content-left">
				<div class="content-left-header">
					<div class="avatar">
						<img :src="user.info.avatar">
					</div>
					<p class="mt-3">{{user.info.client.client_name}}</p>
				</div>	
				<div class="content-left-body">
					<ul>
						<li :class="active('base')" @click="push('/base')">
							<Icon type="document-text" size="30"></Icon>
							<p class="mt-2">基本资料</p>
						</li>
						<li :class="active('renew')" @click="push('/renew')">							
							<Icon type="android-time" size="30"></Icon>
							<p class="mt-2">续费</p>
						</li>
						<li :class="active('expand')" @click="push('/expand')">							
							<Icon type="ios-infinite" size="30"></Icon>
							<p class="mt-2">扩容</p>
						</li>
						<!--
						<li :class="active('increment')" @click="push('/increment')">
							<Icon type="ios-cart-outline" size="30"></Icon>
							<p class="mt-2">增值服务</p>
						</li>
						-->
					</ul>
				</div>
			</div>
			<div class="content-right">
				<div class="content-right-title">
					<span v-html="title" @click="back"></span>
					<Button @click="logout" class="pull-right" icon="log-out" type="primary">退出</Button>
				</div>
				
				<router-view></router-view>
			</div>			
		</div>
		<app-footer></app-footer>
	</div>
	
</template>

<script>	
	import appHeader from './common/header.vue'
	import appFooter from './common/footer.vue'
	import api from '@/api/common'
	
	export default{
		components: {
			appHeader,
			appFooter
		},
		data () {
			return {
				
			}
		},
		methods: {
			push (uri) {
				this.$router.push(uri)
			},
			active (uri) {
				return this.$route.name.indexOf(uri)>-1?'active':''
			},
			back () {
				if(this.$route.meta.need_back){
					this.$router.back()					
				}
			},
			logout(){
				api.Logout().then(response=>{
	        		this.$store.commit('clearLoginInfo')
	           		this.$router.replace({path:'/login'})
	        	},response=>{
	        		this.$store.commit('clearLoginInfo')
	           		this.$router.replace({path:'/login'})
	        	})
			}
		},
		computed: {	
			user(){
				return this.$store.state.user
			},	
			title () {
				if(this.$route.meta.need_back){
					return `<i class="ivu-icon ivu-icon-chevron-left mr-2"></i>返回`
				}
				return this.$route.meta.title
			},
			margin () {
				let height = document.body.clientHeight||document.documentElement.clientHeight,
					margin = (height - 720)/2
				
				return margin
			}
		}
	}
</script>



